const fruit = require('../static/fruit.json');
const projects = fruit.projects;
const projectsLength = projects.length;

const list = (projects) => {
  for (var i = 0; i < projectsLength; i++) {
  return (
    <li><a href={projects[i].url} target="_blank">{projects[i].url}</a></li>
    )
}};

export default () => (
  <ul>
    {list}
  </ul>
);